.article-prev p{
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 300px;
  max-height: 40px;
  margin-top: 5px;
}
.thumb {
    height: 150px;
    background-size: cover;
    background-position: center;
}
.article-prev h2{max-width: 300px;}
.article-prev{
  display: flex;
  max-width: 1000px;
  position: relative;
  left: 50%;
  transform: translateX(-50%) skewy(3deg);
  flex-wrap: wrap;
}
.article-prev div:hover{
  transform: translateY(-1%);
  transition: .3s
}

.article-prev div a{
  text-decoration: none
}
.article-prev div a:hover{
  text-decoration:  underline
}
em{
  color: #aaa !important;
  font-size: 12px;
  letter-spacing: 3px
}
.article-prev div{
  margin: 0px;
  padding: 10px;
  cursor: pointer;
}

.article-prev div img{
  width: 295px;
  height: 150px;
  border-radius: 8px
}
#blog{
  display: none;
  background: #fff;
  margin-top: 150px;
  transform: skewy(-3deg)
}
#blogg{
  transform: skewy(3deg) translateX(-50%) translateY(-30px);
  text-align: center;
  padding: 20px;
  background: #1c2334;
  width: 100px;
  position: relative;
  left: 50%;
  color: white !important;
}
#blog *{ color: #1c2334 }
@media screen and (max-width:639px){
  .article-prev{
    padding-left: 20px;
    padding-right: 30px;
  }
  .article-prev div{
    width: 100%;
  }
}