.full-post{
  height: 100%;
  width: 100%;
  max-width:1200px;
  color: #1c2334;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  padding: 50px;
  background: white 
}

.featured{
  height: 405px;
  width: 720px;
  background-size: cover;
  background-position: center;
  margin: auto;
}
.post{
  margin-bottom: 20px;
  border-bottom: 1px solid #bbb;
  padding-bottom: 50px;
  display: none;
}
#unm{
  position: sticky;
  top: 50px;
  float: right;
  transform: translateX(40px);
  background: transparent;
  border: 0;
  font-weight: bold;
  font-size: 25px;
  cursor: pointer;
}
#fullBlog{
  background: #eee;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 99;
  padding-bottom: 100px
}
.post h2{
  font-size: 45px
}

.post p{
  padding: 90px;
  font-size: 18px;
  line-height: 1.4
}

em{
  line-height: 0
}