.arrow {
  border: solid  white;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 3px;
  background: transparent;
  left: 50%;
  animation: blink 1s infinite;
  height: 5px;
  width: 5px;
  position: absolute;
  bottom: 100px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}


.arrow2 {
  border: solid  white;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 3px;
  background: transparent;
  left: 50%;
  animation: blink 1s infinite;
  height: 5px;
  width: 5px;
  position: absolute;
  bottom: 10px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

@keyframes blink {
  0%{
    opacity: 0
  }
  100%{
    opacity: 1
  }
}

